import React from 'react';
import {connect} from "react-redux";
import {
    addPostactionCreatore,
    getStatus,
    getUsersProfileCreator,
    savePhoto,
    saveProfile,
    setUserProfile,
    updateStatus
} from "../../Redax/profile-reducer";
import Profile from "./Profile";
import {withRouter} from "react-router-dom";
import {compose} from "redux";
import TestError from "../common/testError/testError";
import {
    getAuth,
    getErrorTest, getLifeEvent,
    getPostsname, getProfileState,
    getSmallIcons,
    getSocialIcons, getStatusState,
    getUpdateStatusProfile, getUserIdState
} from "../../Redax/profile-selectors";


class ProfileContainer extends React.Component {

    refreshProfile() {

        let userId = this.props.match.params.userId;
        if (!userId) {
            userId = this.props.authorisedUserId;
            if (!userId) {
                userId = this.props.history.push('/login');
            }
        }
        this.props.getUsersProfileCreator(userId);
        this.props.getStatus(userId);
    }

    componentDidMount() {

        this.refreshProfile()
    }

    componentDidUpdate(prevProps, prevState, snapshot) {

        if (this.props.match.params.userId != prevProps.match.params.userId) {
            this.refreshProfile()
        }
    }


    render() {

        if (this.props.errorTest) {
            return <TestError errorTest={this.props.errorTest}/>
        }

        return (
            <Profile {...this.props} isOwner={!this.props.match.params.userId}
            />
        )
    }
}


let mapStateToProps = (state) => {
    return ({
        profile: getProfileState(state),
        status: getStatusState(state),
        isAuth: getAuth(state),
        authorisedUserId: getUserIdState(state),
        updateStatusProfile: getUpdateStatusProfile(state),
        errorTest: getErrorTest(state),
        postsName: getPostsname(state),
        smallIcons: getSmallIcons(state),
        socialIcons: getSocialIcons(state),
        lifeEvent: getLifeEvent(state),
    })
};

//конвейр обработчиков над ProfileContainer,выполняется снизу вверх, сначало connect затем результат к withRouter, дальше по аналогии
//withAuthRedirect,//дублирующая логика-авторизован или нет, создали hoc для переиспользования кода, кому это нужно
export default compose(
    withRouter,
    connect(mapStateToProps, {
        setUserProfile, getUsersProfileCreator, getStatus,
        updateStatus, savePhoto, saveProfile, addPostactionCreatore
    }),
)(ProfileContainer);
