const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_POST:
            let newPost = {
                id: 4,
                src1: photo,
                name: action.newPost,
                name1: 'SaitBot',
                name2: '10 hour ago',
                src2: 'https://avatars.mds.yandex.net/get-zen_doc/237236/pub_5d1b95137782bf00adbe3694_5d1b9522f221ef00adfa605d/scale_1200',
                p3: '',
                src3: min,
                src4: 'https://iqonic.design/themes/socialv/html/images/icon/01.png',
                span1: '0 Likes',
                time1: '0 Comment',
                src5: 'https://e7.pngegg.com/pngimages/299/561/png-clipart-share-icon-computer-icons-share-button-black-and-white-share-button.png',
                text1: '0 Share',
                src6: 'https://iqonic.design/themes/socialv/html/images/user/02.jpg',
                text2: 'Monty Carlo',
                text3: 'Loren ipsum dolor sit amet',
                span2: '0 min',
                src7: 'https://iqonic.design/themes/socialv/html/images/user/03.jpg',
                text4: 'Paul Molive',
                text5: 'Loren ipsum dolor sit amet',
                span3: '0 min',
            };
            return {
                ...state,
                Postsname: [...state.Postsname, newPost],
            };
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.profile
            };
        case SET_STATUS:
            return {
                ...state,
                status: action.status,
            };
        case SET_UPDATE_STATUS_PROFILE:
            return {
                ...state,
                updateStatusProfile: action.updateStatusProfile,
            };
        case DELETE_POST:
            return {
                ...state,
                Postsname: state.Postsname.filter(p => p.id != action.postId)
            };
        case SAVE_PHOTOS_SUCCESS:
            return {
                ...state,
                profile: {...state.profile, photos: action.photos}
            };

        default:
            return state;
    }
}


export const addPostactionCreatore = (newPost) => ({type: ADD_POST, newPost});
export const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile});
export const setStatus = (status) => ({type: SET_STATUS, status});
export const setUpdateStatusProfile = (updateStatusProfile) => ({type: SET_UPDATE_STATUS_PROFILE, updateStatusProfile});
export const deletePost = (postId) => ({type: DELETE_POST, postId});
export const savePhotoSuccess = (photos) => ({type: SAVE_PHOTOS_SUCCESS, photos});

export const getStatus = (userId) => {
    return async (dispatch) => {
        let response = await profileAPI.getStatus(userId);
        dispatch(setStatus(response.data));
    }
}


export const updateStatus = (status) => {
    return async (dispatch) => {
        try {
            let response = await profileAPI.updateStatus(status);
            if (response.data.resultCode === 0) {
                dispatch(setStatus(status));
            }
        } catch (err) {
            alert(err)
        }

    }
}//0 означает по API документации, что сервер говорит вы успещно обновили статус на сервере,
// "вешаем на ассихронный запрос" обработчик ошибки try/catch,если что то пойдет не так выведет ошибку
// в алерт показать пользователю ошибку либо могли какие то действия принят, можно сделать редирект на логинизацию например

export const getUsersProfileCreator = (userId) => {
    return async (dispatch) => {
        let response = await usersAPI.getProfile(userId);
        dispatch(setUserProfile(response.data));
    }
}//thunk creator функция, к-я возвращает другую функцию и принимает параматр,
//внутренная функция это thunk, ее задача асихированная опирации и дистачить action (c функцией action creator)
export const savePhoto = (filePhoto) => {

    return async (dispatch) => {
        let response = await profileAPI.savePhoto(filePhoto);
        if (response.data.resultCode === 0) {
            dispatch(savePhotoSuccess(response.data.data.photos));
        }
    }
}
export const saveProfile = (profile) => {

    return async (dispatch, getState) => {

        const response = await profileAPI.saveProfile(profile);
        const userId = getState().auth.userId
        if (response.data.resultCode === 0) {
            dispatch(getUsersProfileCreator(userId));
            dispatch(setUpdateStatusProfile("Success"));
        } else {
            dispatch(stopSubmit('edit-profile', {_error: response.data.messages[0]}))
            dispatch(setUpdateStatusProfile("Error"))
        }
    }
}

export default profileReducer;