import React from "react";
import f from "./formsControls.module.css";
import s from "./../../Profile/UserProfile/UserProfile.module.css";


export const TextareaEditProfile = ({input, meta: {touched, error}, ...props}) => {
    const hasError = touched && error;
    return (
        <div className={f.formControl + ' ' + (hasError ? f.error : '')}>
            <div><textarea className={s.form_control} {...input} {...props} /></div>
            {hasError && <span className={f.formSummerError}>{error}</span>}

        </div>
    )
}
export const InputEditProfile = ({input, meta: {touched, error}, ...props}) => {
    const hasError = touched && error;
    return (
        <div className={f.formControl + ' ' + (hasError ? f.error : '')}>
            <div><input {...input} {...props} className={s.form_control}/></div>
            {hasError && <span>{error}</span>}

        </div>
    )
}

