import React from 'react';
import s from './Event.module.css';
import {NavLink} from "react-router-dom";

const Events = ({lifeEvent}) => {

    const Event = lifeEvent.map(m => {
        return <div className={s.lifeEvent} key={m.id}>
            <div><img src={m.src}/></div>
            <div className={s.textTime}>
                <div><h3>{m.text}</h3></div>
                <div><h4>{m.time}</h4></div>
            </div>
        </div>
    })

    return <div className={s.event}>
        <div className={s.title}>
            <div className={s.titleName}><h2>Life Event</h2></div>
            <div className={s.titleNav + ' ' + s.active}><NavLink to='/profile'>Create</NavLink></div>
        </div>
        {Event}
    </div>
}
export default Events;