import React from 'react';
import './App.css';
import {HashRouter, Route, withRouter} from "react-router-dom";
import HeaderContainer from "./components/Header/HeaderContainer.js";
import ProfileContainer from "./components/Profile/ProfileContainer";
import Login from "./components/Login/Login";
import {compose} from "redux";
import {connect, Provider} from "react-redux";
import {initializeApp} from "./Redax/app-Reducer";
import Preloader from "./components/common/Preloader/Preloader";
import store from "./Redax/redax-store";
import {withSuspense} from "./components/hoc/withSuspense";
import OnlineContainer from "./components/Online/OnlineContainer";
import NavbarContainer from "./components/Navbar/NavbarContainer";


const Dialogs = React.lazy(() => import("./components/Dialogs/Dialogs.js"));
//import Dialogs from "./components/Dialogs/Dialogs.js";

const UsersContainer = React.lazy(() => import("./components/Users/UsersContainer"));
//import UsersContainer from "./components/Users/UsersContainer";
//lazy метод, в Bundle они не будут только при клике/зароса к юзеру загрузиться файлы


class App extends React.Component {

    componentDidMount() {

        this.props.initializeApp();

    }

    componentWillUnmount() {

    }


    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }

        return (
            <div className='app-wrapper'>
                <HeaderContainer/>
                <NavbarContainer/>
                <OnlineContainer/>
                <div className='app-wrapper-content'>
                    <Route path='/profile/:userId?' render={() => <ProfileContainer/>}/>
                    <Route path='/dialogs' render={withSuspense(Dialogs)}/>
                    <Route path='/users' render={withSuspense(UsersContainer)}/>
                    <Route path='/login' render={() => <Login/>}/>
                </div>
            </div>

        )
    }
}

const mapStateToProps = (state) => {
    return {
        initialized: state.app.initialized,
    }
}

const AppContainer = compose(
    withRouter,
    connect(mapStateToProps, {initializeApp})
)(App);


const SamuraiJSApp = (props) => {
    return <HashRouter>
        <Provider store={store}>
            <AppContainer/>
        </Provider>
    </HashRouter>
}

export default SamuraiJSApp;