import React from 'react';
import {Field, reduxForm, reset} from "redux-form";
import s from './../CreatePost.module.css';
import {maxLengthCreator, required} from "../../../../utils/validations/validator";
import {Textarea} from "../../../common/formsControls/formsControls";

const maxLength10 = maxLengthCreator(10);

const AddNewPostForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit} className={s.content_form}>
            <div className={s.content_text}>
                <Field name={'newPost'} component={Textarea} placeholder={'Write something here...'}
                       className={s.formNewPost} validate={[required, maxLength10]}/>
            </div>
            <div className={s.content_post}>
                <button>Post</button>
            </div>
        </form>
    )
}
const afterSubmit = (result, dispatch) =>
    dispatch(reset('profile')); // после успешной отправки, обнуляем поля нашей формы

export const AddPostReduxForm = reduxForm({form: 'profile', onSubmitSuccess: afterSubmit,})(AddNewPostForm);

