import React from 'react';
import calendar from "./../axios/images/header_kalendar.jpg";
import music from "./../axios/images/header_music.jpg";
import menu from "./../axios/images/header_menu.jpg";

const SET_ICONS_HEADER = 'savetooths/headerReducer/ SET_ICONS_HEADER';

let initialState = {
    ArrIconsHeader: [
        {id: 1, src: music},
        {id: 2, src: calendar},
        {id: 3, src: menu},

    ]
}

const headerReducer = (state = initialState, action) => {

    switch (action.type) {

        case  SET_ICONS_HEADER:
            return {
                ...state,
                ArrIconsHeader: [...state.ArrIconsHeader, action.body]
            }
        default:
            return state;
    }
}

export const setHeaderReducer = (body) => ({type: SET_ICONS_HEADER, body});


export const getOnlinePerson = (body) => (dispatch) => {
    //запрос на DAL уровень-заглушка
    dispatch(setHeaderReducer(body))
}


export default headerReducer;


