import React from 'react';
import {getAuthUserData} from "./authReducer";
import {updateStatus} from "./profile-reducer";


const INITIALIZED_SUCCESS = 'savetooths/appReducer/INITIALIZED_SUCCESS';
const ERROR_TEST = 'savetooths/appReducer/ERROR_TEST';

let initialState = {
    initialized: false,
    errorTest: null,
};

const appReducer = (state = initialState, action) => {

    switch (action.type) {

        case INITIALIZED_SUCCESS:
            return {
                ...state,
                initialized: true,
            }
        case ERROR_TEST:
            return {
                ...state,
                errorTest: action.error,
            }
        default:
            return state;
    }
}

export const initializedSuccess = () => ({type: INITIALIZED_SUCCESS});
export const setTestError = (error) => ({type: ERROR_TEST}, error);

export const initializeApp = () => (dispatch) => {
    let promise = dispatch(getAuthUserData());
    //dispatch(example)
    //dispatch(example)
    Promise.all([promise])//паралелльно всех запустили, дождались - результата и тогда запускаем dispatch
        .then(dispatch(initializedSuccess()))
}

export const getTestError = (error) => (dispatch) => {
    dispatch(setTestError(error))
}

export default appReducer;


