import React from 'react';
import s from './CreatePost.module.css';
import Post from './Postt/Post'
import {AddPostReduxForm} from "./AddPostForm/AddNewPost";
import MyPhotoNavlink from "../../common/MyPhotoNavlink/MyPhotoNavlink";


const CreatePost = ({profile, postsName, smallIcons, addPostactionCreatore}) => {


    const arrSmallIcons = smallIcons.map(s => {
        return <div className={s.footerCreatePost1} key={s.id}>
            <img src={s.src}/>
            <p>{s.p}</p>
        </div>
    })

    let newPost = (values) => {
        addPostactionCreatore(values.newPost)
    };


    let postsElements = postsName.map(p => <Post id={p.id}
                                                 src1={p.src1}
                                                 name={p.name}
                                                 name1={p.name1}
                                                 name2={p.name2}
                                                 src2={p.src2}
                                                 p3={p.p3}
                                                 src3={p.src3}
                                                 src4={p.src4}
                                                 span1={p.span1}
                                                 time1={p.time1}
                                                 src5={p.src5}
                                                 text1={p.text1}
                                                 src6={p.src6}
                                                 text2={p.text2}
                                                 text3={p.text3}
                                                 span2={p.span2}
                                                 src7={p.src7}
                                                 text4={p.text2}
                                                 text5={p.text3}
                                                 span3={p.span2}
                                                 key={p.id}
    />)

    return <div className={s.posts}>
        <div className={s.createPost}>
            <div className={s.header}>
                <h3>Create Post</h3>
            </div>
            <div className={s.content_ava}>
                <div className={s.content1}>
                    <MyPhotoNavlink profile={profile}/>
                </div>
                <AddPostReduxForm onSubmit={newPost}/>
            </div>
            <div className={s.footerCreatePost}>
                {arrSmallIcons}
            </div>
        </div>
        <div className={s.post}>
            {postsElements}
        </div>
    </div>
}


export default CreatePost;