import React from 'react';
import {reduxForm} from "redux-form";
import {Input} from "../common/formsControls/formsControls";
import {required} from "../../utils/validations/validator";
import {connect} from "react-redux";
import {login} from "../../Redax/authReducer";
import {Redirect} from "react-router-dom";
import s from "./../common/formsControls/formsControls.module.css";
import {createField} from "../common/createField/createField";
import FriendsLogo from "./../../axios/images/Friends_list2.png"


const LoginForm = ({handleSubmit, error, captchaUrl}) => {

    return (
        <form onSubmit={handleSubmit} className={s.MainForm}>
            {createField("Email", "email", [required], Input)}
            {createField("Password", "password", [required], Input, {type: "password"})}
            {createField(null, "rememberMe", [], Input, {type: "checkbox"}, "Remember me")}
            {captchaUrl && <img src={captchaUrl}/>}
            {captchaUrl && createField("Symbols from image", "captcha", [required], Input, {})}
            {error && <div className={s.formSummerError}>
                {error}
            </div>}
            <div>
                <button className={s.headerLogin}>Log In</button>
            </div>

        </form>
    )
}

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm);

const Login = (props) => {
    const onSubmit = (FormData) => {
        props.login(FormData.email, FormData.password, FormData.rememberMe, FormData.captcha)
    }//передаем данные в callback функцию из mapDispatchToProps, просто одноименный запись с thunk

    if (props.isAuth) {
        return <Redirect to='/profile'/>
    }//если мы уже залогилинились то перейдем в свой profile

    return <div className={s.loginForm}>
        <div className={s.Logotip}>
            <div className={s.LogotipName}><img src="https://iqonic.design/themes/socialv/html/images/logo-full.png"/>
            </div>
            <div className={s.FriendsLogo}><img src={FriendsLogo}/></div>
            <div>
                <h1>Find new friends</h1>
                <p>SocialIV helps you connect and share with the people in your life.</p>
            </div>
        </div>
        <div className={s.loginBlock}>
            <div className={s.loginBlock1}><h2>Log In</h2></div>
            <LoginReduxForm onSubmit={onSubmit} captchaUrl={props.captchaUrl}/>
        </div>
    </div>
}

const mapStateToProps = (state) => {
    return {
        isAuth: state.auth.isAuth,
        captchaUrl: state.auth.captchaUrl,
    }
}

export default connect(mapStateToProps, {login})(Login); //отправляю данные через thunk Login, на сервер для авторизации