import React from "react";
import {NavLink} from "react-router-dom";
import userPhoto from "../../../axios/images/user.png";
import s from "./MyPhotoNavlink.css";

const MyPhotoNavlink = (props) => {
    return <div>
        <NavLink to="/profile">
            <img src={props.profile != null ? props.profile.photos.large : userPhoto} className={s.photo}/>
        </NavLink>
    </div>
}
export default MyPhotoNavlink