import React from "react";
import s from "./Header.module.css";
import {NavLink} from "react-router-dom";
import {HeaderSearchReduxForm} from "./HeaderSearchForm/HeaderSearchForm";
import MyPhotoNavlink from "../common/MyPhotoNavlink/MyPhotoNavlink";


const Header = (props) => {
    const ArrIconsHeader = props.ArrIconsHeader.map(e => {
        return (<div key={e.id} className={s.navIcons1}><img
            src={e.src}/></div>)
    })


    const newSearch = (values) => {
        alert(values.headerSearch)
    }
    return (
        <div className={s.header}>
            <div className={s.headerLogo}>
                <img src="https://iqonic.design/themes/socialv/html/images/loader.png" alt='Ava'/>
            </div>
            <div className={s.headerSearch}>
                <HeaderSearchReduxForm onSubmit={newSearch}/>
            </div>
            <div className={s.headerNav}>
                {ArrIconsHeader}
                <div className={s.photo}>
                    <MyPhotoNavlink profile={props.profile}/>
                </div>
            </div>
            <div>
                {props.isAuth
                    ? <div className={s.navIcons_login}>
                        {props.login}
                        <button className={s.headerSearch} onClick={props.logout}>Log out</button>
                    </div>
                    : <NavLink to='/login'>Login</NavLink>
                }
            </div>
        </div>
    )
};
export default Header;