import React from 'react';
import s from './Online.module.css';
import {NavLink} from "react-router-dom";


const Online = (props) => {

    const arrayOnline = props.onlinePerson.map(n => {
        return <div key={n.id}>
            <NavLink to={n.urlName}><img
                src={n.src}/>
                <h6>{n.h6}</h6>
            </NavLink>
        </div>
    })

    return <div className={s.online}>
        {arrayOnline}
    </div>
}

export default Online;