import React from 'react';
import s from './../CreatePost.module.css'
import smile from "./../../../../axios/images/smiley.png"


const Post = (props) => {

    return <div className={s.post1}>
        <div className={s.header}>
            <div className={s.header1}>
                <img src={props.src1} alt=""/>
            </div>
            <div className={s.header2}>
                <div>
                    <a href="#"><h3>{props.name}</h3></a></div>
                <div>{props.name1}</div>
                <div>{props.name2}</div>
            </div>
            <div className={s.header3}>
                <a href='#'><img
                    src={props.src2} alt=""/></a>
            </div>
        </div>
        <div className={s.content}>
            <img src={props.src3} alt=""/>
            <div>{props.p3}</div>
        </div>
        <div className={s.footer}>
            <div className={s.like}>
                <div className={s.like1}>
                    <a href="#"><img src={props.src4} alt=""/></a>
                    <span>{props.span1}</span>
                </div>
                <div className={s.like2}>
                    <a href="#">{props.time1}</a>
                </div>
                <div className={s.like3}>
                    <a href="#"><img
                        src={props.src5} alt=""/>
                        {props.text1}
                    </a>
                </div>
            </div>
            <div className={s.post1_comment}>
                <div className={s.comment}>
                    <div>
                        <img src={props.src6} alt=""/>
                    </div>
                    <div>
                        <div className={s.user1}>
                            <h6>{props.text2}</h6>
                            <p>{props.text3}</p>
                            <div className={s.user1_a}>
                                <a href="#">Like</a>
                                <a href="#">Reply</a>
                                <a href="#">Translate</a>
                                <span>{props.span2}</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={s.comment}>
                    <div>
                        <img src={props.src7} alt=""/>
                    </div>
                    <div>
                        <div className={s.user1}>
                            <h6>{props.text4}</h6>
                            <p>{props.text5}</p>
                            <div className={s.user1_a}>
                                <a href="#">Like</a>
                                <a href="#">Reply</a>
                                <a href="#">Translate</a>
                                <span>{props.span3}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className={s.add_comment + ' ' + s.active}>
                <div className={s.add_comment1}>
                    <input type="text" className={s.add_comment1}/>
                </div>
                <div className={s.add_comment2}>
                    <a href="#"><img src="https://img.icons8.com/fluent-systems-regular/2x/attach.png"/></a>
                    <a href="#"><img
                        src={smile}/></a>
                    <a href="#"><img
                        src="https://cdn.icon-icons.com/icons2/37/PNG/128/slr_camera_application_slr_3037.png"/></a>
                </div>
            </div>
        </div>
    </div>
}
export default Post;