import React from 'react';
import UserProfile from "./UserProfile/UserProfile";
import MyProfile from "./MyProfile/MyProfile";


const Profile = (props) => {

    return (
        <div>
            <UserProfile profile={props.profile}
                         status={props.status} updateStatus={props.updateStatus}
                         isOwner={props.isOwner} savePhoto={props.savePhoto}
                         saveProfile={props.saveProfile} updateStatusProfile={props.updateStatusProfile}
                         socialIcons={props.socialIcons}
            />
            <MyProfile profile={props.profile} postsName={props.postsName} smallIcons={props.smallIcons}
                       addPostactionCreatore={props.addPostactionCreatore} lifeEvent={props.lifeEvent}
            />
        </div>
    )
}
export default Profile;

