import React from "react";
import {Field, reduxForm} from "redux-form";
import s from "./UserProfile.module.css";
import {required} from "../../../utils/validations/validator";
import {InputEditProfile, TextareaEditProfile} from "../../common/formsControls/formEditProfile";


const ProfileDataForm = ({handleSubmit, profile, error, touched, setEditMode}) => {

    const renderError = () => touched && error ? <span>{error}</span> : false

    return <form onSubmit={handleSubmit}>
        <div className={s.buttonEditMode}>
            <button className={s.editMode + " " + s.active}>Save</button>
        </div>
        {error && <div className={s.Error}>
            {error}</div>}
        <div className={s.profileData}>
            <div>
                <div>
                    <h3>Full name:</h3><Field type="text" placeholder="Full name" name="fullName"
                                              component={InputEditProfile} validate={[required]}/>
                </div>
                <div>
                    <label><h3>Looking for a job</h3></label>
                    <div className={s.lookingForAJob}>
                        <label><Field name="lookingForAJob" component="input" type="radio" value="yes"/> Yes</label>
                        <label><Field name="lookingForAJob" component="input" type="radio" value="no"/> No</label>
                        <Field name="lookingForAJob" component={renderError}/>
                    </div>
                </div>
                <div>
                    <h3>My professional skills:</h3><Field placeholder={"My professional skills"}
                                                           name={"lookingForAJobDescription"}
                                                           component={InputEditProfile} validate={[required]}

                />
                </div>
                <div>
                    <h3>About me:</h3><Field placeholder={"About me"} name={"aboutMe"} component={TextareaEditProfile}
                                             validate={[required]}
                />
                </div>
            </div>
            <div>
                <div>
                    {Object.keys(profile.contacts).map(key => {
                        return <div key={key}>
                            <h3>{key}:</h3>
                            <Field placeholder={key} name={"contacts." + key} component={InputEditProfile}

                            />
                        </div>
                    })}
                </div>
            </div>
        </div>
    </form>
}

//1.вызывает e.preventDefault-не перезапускает на каждый изменения перезагрузку 2.собирает значения
//3. С собранными значения из форма запускаем props.onSubmit(собранные значения)к-е пришел из redux-form и отправляется собранные значения родительской компоненте
//что родительский компонент вызывавшийся ProfileDataFormRedux взял значения и dispatch в store где сидит state

const ProfileDataFormRedux = reduxForm({form: 'edit-profile'})(ProfileDataForm);


export default ProfileDataFormRedux;