import React from 'react';

const SET_ONLINE_PERSON = 'savetooths/appReducer/SET_ONLINE_PERSON';

let initialState = {
    onlinePerson: [
        {id: 1, urlName: '/profile', src: 'https://iqonic.design/themes/socialv/html/images/user/01.jpg', h6: 'Alina'},
        {id: 2, urlName: '/profile', src: 'https://iqonic.design/themes/socialv/html/images/user/02.jpg', h6: 'Radik'},
        {id: 3, urlName: '/profile', src: 'https://iqonic.design/themes/socialv/html/images/user/03.jpg', h6: 'Lesya'},
        {id: 4, urlName: '/profile', src: 'https://iqonic.design/themes/socialv/html/images/user/04.jpg', h6: 'Ralina'},
        {id: 5, urlName: '/profile', src: 'https://iqonic.design/themes/socialv/html/images/user/05.jpg', h6: 'Vadim'},
        {id: 6, urlName: '/profile', src: 'https://iqonic.design/themes/socialv/html/images/user/06.jpg', h6: 'Kamila'},
        {id: 7, urlName: '/profile', src: 'https://iqonic.design/themes/socialv/html/images/user/07.jpg', h6: 'Ruslan'},
        {id: 8, urlName: '/profile', src: 'https://iqonic.design/themes/socialv/html/images/user/08.jpg', h6: 'Karina'},
        {id: 9, urlName: '/profile', src: 'https://iqonic.design/themes/socialv/html/images/user/09.jpg', h6: 'Dinar'},
    ]
}

const onlineReducer = (state = initialState, action) => {

    switch (action.type) {

        case SET_ONLINE_PERSON:
            return {
                ...state,
                onlinePerson: [...state.onlinePerson, action.body]
            }
        default:
            return state;
    }
}

export const setOnlinePerson = (body) => ({type: SET_ONLINE_PERSON, body});


export const getOnlinePerson = (body) => (dispatch) => {
    //запрос на DAL уровень-заглушка
    dispatch(setOnlinePerson(body))
}


export default onlineReducer;


